* {
    box-sizing: border-box;
}

html, body {
    margin: 0;
    width: 100%;
    background: #cccca2;
}

.container {
    max-width: 260px;
    margin: 0 auto;

    @media (min-width: 760px) {
        max-width: 630px;
    }

    @media (min-width: 1024px) {
        max-width: 900px;
    }
}


/* ------------ title ------------ */

.title {
    margin: 0;
    padding: 50px 0;
    font-family: 'Amatic SC', cursive;
    text-decoration: underline;
    text-align: center;
    font-size: 90px;
    line-height: 90px;
    color: #800000;
}


/* ------------ gallery ------------ */

.gallery {
    margin-top: 50px;
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.image {
    width: 270px;
    height: 340px;
    background: #FFF9F9;
    padding: 10px;
    box-shadow: 1px 1px 5px  black;
    position: relative;
    margin-bottom: 60px;
    filter: sepia(60%);
}

.image:before {
    content: '';
    display: inline-block;
    position: absolute;
    width: 90px;
    height: 25px;
    background-color: rgba(236, 236, 236, 0.6);
    box-shadow: 0 1px grey;
    top: -1px;
    left: -26px;
    transform: rotate(-32deg);
    z-index: 2;
}

.image:after {
    content: '';
    display: inline-block;
    position: absolute;
    width: 90px;
    height: 25px;
    background-color: rgba(236, 236, 236, 0.6);
    box-shadow: 0 1px grey;
    bottom: -3px;
    right: -21px;
    transform: rotate(-22deg);
    z-index: 3;
}

.img1 {
    transform: rotate(-5deg);
}

.img2 {
    transform: rotate(2deg);
}

.img3 {
    transform: rotate(6deg);
}

.img4 {
    transform: rotate(4deg);
}

.img5 {
    transform: rotate(-1deg);
}

.img6 {
    transform: rotate(3deg);
}


.photo {
    display: inline-block;
    width: 250px;
    height: 250px;
    filter: sepia(60%);
}

.photo:hover {
    filter: none;
}

.description, 
h3 {
    font-family: 'Caveat', cursive;
    font-size: 35px;
    margin: 0;
    color: #000000;
    padding: 10px 0;
}

/* ------------ lightbox ------------ */

.lightbox {
    width:100%;
    height:100%;
    background: rgba(0,0,0,0.8);
    position: fixed;
    top:0;
    left:0;
    z-index: 10000;
    display: flex;
    justify-content: center;
    align-items: center;
}

    .lightbox__container {
        min-width:200px;
        min-height:200px;
        background: #fff;
        box-shadow: 0 2px 5px rgba(0,0,0,0.7);
        padding:20px;
        position: relative;
    }

        .lightbox__close {
            position: absolute;
            right:-10px;
            top: -5px;
            width:40px;
            height:40px;
            background: #970000;
            border: none;
            font-size:20px;
            color:#fff;
            box-shadow: 0 2px 5px rgba(0,0,0,0.7);
            cursor: pointer;
        }

        .lightbox__img {
            max-height:80vh;
        }
        
            img {
                max-height: 80vh;
                max-width: 100%;
            }


/* ------------ footer ------------ */

footer {
    text-align: center;
    padding: 20px;
}
 .gitlink {
     text-decoration: none;
     color: black;
 }


